.form-reservation{
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  width: 380px;
  height: auto;
  padding: 43px 29px 30px 29px;
  
  display: flex;	
  flex-wrap: wrap;
  justify-content: space-between;

  h3{
    margin: 19px 0 0 0;
  }

  p{
    margin: 0;
  }

  .form-reservation__block{
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: relative;
    align-items: baseline;

    p{

      &:nth-child(1){
        font-weight: 700;
        color: rgba(31, 32, 65, 1) ;

        span{

          &:nth-child(2){
            font-size: 24px;
          }
        }
      }

      &:nth-child(2){
        font-weight: 400;
        font-size: 12.5px;
        color: rgba(31, 32, 65, 0.5) ;

        span{

          &:nth-child(1){
            font-size: 15px;
          }
        }
      }

      span{

        &:nth-child(1),
        &:nth-child(2){
          font-weight: bold;
        }
      }

      a{
        margin: 0 0 0 5px;
      }
    }

    .form-reservation__dashed{
      border: 1px dashed rgba(31, 32, 65, 0.25) ;
      height: 0;
      width: 100%;
      box-sizing: border-box;
      margin: 0 5px;
    }

    h2{
      white-space: nowrap;
      font-size: 19.8px;
      font-weight: 700;
    }
  }

  .form-reservation__block-prices{
    margin: 20px 0 44px;
    width: 100%;
  
    .form-reservation__line-price{
      width: 100%;
      display: flex;
      justify-content: space-between;
      position: relative;
      margin: 10px 0;

      &:first-child{
        margin: 0;
      }

      &:last-child{
        margin: 0;
      }
    
      .form-reservation__text-price{
        display: flex;

        .form-reservation__icon-info-price{
          width: 20px;
          height: 20px;
          margin: 0 9px;
          border: 1px solid rgba(31, 32, 65, 0.25) ;
          box-sizing: border-box;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          color: rgba(31, 32, 65, 0.25) ;
          cursor: pointer;
        }
      }
    }
  }

  .dropdown-dates{

    .calendar-block{

      .calendar-block__btns{
        width: 301px;
        transform: translateX(-1.2px);
      }
    }
  }

  .btn{
    width: 100%;
    height: 36px;
    line-height: 37px;
    margin: 20px 0 0 0;
    font-weight: 700;

    .btn__link{
      padding-right: 5px;
      line-height: 35px;
    }
  }
}