.comment-block{
  width: 100%;
  margin: 13px 0 33px 0;

  .comment-block__title{
    display: flex;
    justify-content: space-between;
    margin: 0;
    height: 22px;
    
    h2{
      line-height: 21px;
    }

    p{
      margin: 0 44px 0 0;
    }
  }

  .comment-block__comment{
    margin: 20px 0 23px;

    &:last-child{
      margin-bottom: 0;
    }

    .comment-block__person{
      display: flex;
      flex-direction: row;
      align-items: flex-start;

      .comment-block__photo{
        width: 48px;
        height: 48px;
        margin-bottom: 10px;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        border: 2px solid #FFFFFF;
        filter: drop-shadow(0px 10px 20px rgba(31, 32, 65, 0.1));
      }

      .comment-block__name-time{
        display: flex;
        flex-direction: column;
        margin-left: 10px;

        .comment-block__name{
          font-weight: bold;
          margin: 0;
        }
  
        .comment-block__time{
          margin-bottom: 12px;
          margin: 0;
        }
 
        p{
          font-size: 14px;
        }
      }
    }

    .comment-block__comment-text{
      display: flex;

      .like{
        margin: 2px 0 0 3px;
      }

      .comment-block__text{
        margin: 0;
        margin-left: 15px;
      }

      p{
        width: 86.5%;
        font-size: 14px;
      }
    }
  }
}